___CSS_LOADER_EXPORT___.push([module.id, `* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: Arial, Helvetica, sans-serif;
}

body {
    min-height: 100vh;
    background-color: #8e3b3b;
    display: flex;
    align-items: center;
    justify-content: center;
}

.container {
    background-color: #ffffff;
    width: 340px;
    padding: 20px;
    border-radius: 12px;
    box-shadow: 0 10px 25px rgba(0, 0, 0, 0.25);
}

/* HEADER */
.header {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 20px;
}

#img-id {
    width: 40px;
    margin-bottom: 10px;
}

#title {
    font-size: 20px;
}

/* PASSWORD */
#password-container {
    background-color: #f0f0f0;
    border-radius: 8px;
    padding: 14px;
    text-align: center;
    font-weight: bold;
    margin-bottom: 20px;
    min-height: 45px;

    display: flex;
    align-items: center;
    justify-content: center;
}

#password {
    font-size: 16px;
    color: #333;
    word-break: break-all;
}

/* OPTIONS */
#password-itens {
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.item {
    display: flex;
    align-items: center;
    gap: 8px;
    font-size: 14px;
}

.item input[type="number"] {
    width: 60px;
    padding: 3px;
}

/* BUTTON */
#generate-password {
    margin-top: 15px;
    padding: 10px;
    border: none;
    border-radius: 8px;
    background-color: #ff4d4d;
    color: white;
    font-weight: bold;
    cursor: pointer;
}

#generate-password:hover {
    background-color: #e63b3b;
}
